@import "https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap";
:root {
  --clr-body-bgcolor: #f2f2f2;
  --clr-accent: #fe9501;
  --clr-primary: #000;
  --ff-default: "Manrope", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --fs-100: .625rem;
  --fs-200: .75rem;
  --fs-300: .875rem;
  --fs-400: 1rem;
  --fs-410: 1.125rem;
  --fs-420: 1.25rem;
  --fs-430: 1.375rem;
  --fs-500: 1.625rem;
  --fs-510: 1.75rem;
  --fs-520: 1.875rem;
  --fs-550: 2.188rem;
  --fs-600: 2.5rem;
  --fs-700: 3.125rem;
  --fs-800: 3.75rem;
  --fs-900: 5rem;
}

*, :before, :after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizespeed;
  min-height: 100vh;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

:root {
  --border-radius-100: .188rem;
  --border-radius-200: .5rem;
  --border-radius-300: .625rem;
  --border-radius-400: 1.25rem;
  --border-radius-500: 1.875rem;
  --border-radius-600: 3.125rem;
  --transition-speed-slow: 1s;
  --transition-speed-normal: .5s;
  --transition-timing-function-cubic: cubic-bezier(.55, 0, .1, 1);
  --animation-speed-slow: 1.6s;
  --line-height-title: 1.1;
  --line-height-paragraph: 1.8;
}

.container {
  width: 90%;
  max-width: 1000px;
  margin-inline: auto;
}

.main {
  place-content: center;
  min-height: 100vh;
  display: grid;
}

.btn {
  appearance: none;
  cursor: pointer;
  border-radius: var(--border-radius-600);
  border: none;
  width: 70%;
  margin-inline: auto;
  padding: .5em 1em;
}

.btn--solid {
  background-color: var(--clr-primary);
  color: #fff;
  transition: background-color var(--transition-speed-normal) ease;
  isolation: isolate;
  position: relative;
  overflow: hidden;
}

.btn--solid:hover {
  background-color: var(--clr-accent);
}

.btn--solid:hover:before {
  transform: translateY(0)scale(1.3);
}

.btn--solid:hover:after {
  transform: translateY(0)scale(1.6);
}

.btn--solid:before, .btn--solid:after {
  content: "";
  transition: transform var(--transition-speed-normal) var(--transition-timing-function-cubic);
  z-index: -1;
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.btn--solid:before {
  background-color: #ccc;
  transform: translateY(160%)scale(1.3);
}

.btn--solid:after {
  background-color: var(--clr-accent);
  transition-delay: .1s;
  transform: translateY(180%)scale(1.6);
}

.visually-hidden {
  visibility: hidden;
  height: 0;
  position: absolute;
}

.form {
  gap: 1em;
  display: grid;
}

.form-input {
  border-radius: var(--border-radius-600);
  transition: border var(--transition-speed-normal) ease;
  background-color: #e6e6e6;
  border: 1px solid #999;
  padding: .4em 1em;
}

.form-input:hover {
  border: 1px solid var(--clr-accent);
}

.form-input:focus-within {
  border: 1px solid var(--clr-primary);
}

.form-input input {
  appearance: none;
  border-radius: var(--border-radius-600);
  background: none;
  border: none;
  outline: none;
  width: 100%;
}

.card {
  border-radius: var(--border-radius-200);
  background-color: #fff;
  gap: 1em;
  display: grid;
  position: relative;
  overflow: hidden;
}

@media (width >= 640px) {
  .card {
    width: 100%;
    max-width: 6000px;
  }
}

.card__contents {
  margin-block-end: 2em;
  padding-inline: 1em;
}

.card__img {
  isolation: isolate;
  background-image: url("card-img-bg.44766b59.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  place-content: center;
  min-height: 200px;
  display: grid;
  position: relative;
}

.card__img:after {
  content: "";
  z-index: -1;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #fff3;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

body {
  font-family: var(--ff-default);
  font-size: var(--fs-400);
  background-color: var(--clr-body-bgcolor);
}

